.FlexContainer {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  padding: 20px;
  width: 100%;
}
.FlexItem {
  background: #252525;
  border-radius: 8px;
  padding: 20px;
  height: 300px; /* ทุกกล่องสูงเท่ากัน */
  box-sizing: border-box;
  cursor: pointer;
  overflow: hidden;
  position: relative;
}

@media (max-width: 480px) {
  .FlexContainer {
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    padding: 10px;
  }
}
@media (max-width: 300px) {
  .FlexContainer {
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    padding: 0px;
  }
  .FlexItem{
    border-radius: 0;
  }
}

